@import url(https://fonts.googleapis.com/css?family=Kalam);

body,
html,
div.board,
div#react-container {
    height: 100%;
    overflow: hidden;
}

body {
    margin: 0;
    padding: 0;
}

div.board {
    background-color: brown;
    width: 100%;
    height: 1000px;
    background: #445;
    background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(57%, #445), color-stop(99%, #112));
    background: -webkit-radial-gradient(center, ellipse cover, #445 57%, #112 99%);
    background: -o-radial-gradient(center, ellipse cover, #445 57%, #112 99%);
    background: -webkit-radial-gradient(center, ellipse, #445 57%, #112 99%);
    background: -o-radial-gradient(center, ellipse, #445 57%, #112 99%);
    background: radial-gradient(ellipse at center, #445 57%, #112 99%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#445', endColorstr='#112', GradientType=1);
    cursor: crosshair;
}

div.note {
    height: 150px;
    width: 150px;
    background-color: yellow;
    margin: 2px 0;
    position: absolute;
    cursor: default;
    -webkit-box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, .2);
    box-shadow: 5px 5px 15px 0 rgba(0, 0, 0, .2);
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

div.note p {
    margin: 0;
    font-size: 18px;
    padding: 7px 12px;
    font-family: "Kalam", Arial;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

div.note:hover > span {
    opacity: 1;
}

div.note > span {
    position: absolute;
    bottom: 2px;
    right: 2px;
    opacity: 0;
    -webkit-transition: opacity .25s linear;
    -o-transition: opacity .25s linear;
    transition: opacity .25s linear;
}

div.note button {
    margin: 2px;
    background-color: yellow;
    border: 0;
}

div.note button:hover {
    color: #15f;
}

div.note textarea {
    width: 144px;
    min-height: 50px;
}



